<template lang="pug">
section
  h1
    | Verification Code
  VInput(
    v-model="verificationCode"
    type="number"
    placeholder="123456"
    required
  )
  VButton(
    kind="primary"
    text="Next"
    :disabled="!isValid"
    @click.native="submit"
  )
  p.frow.width-100.text-center
    | {{ error }}
</template>

<script>
export default {
  name: 'RegisterPhoneVerification',
  props: {
    error: String,
  },

  data() {
    return {
      verificationCode: '',
    };
  },

  computed: {
    isValid: state => state.verificationCode.match(/^[0-9]{6}$/),
  },

  methods: {
    submit() {
      this.$emit('submit', {
        verificationCode: this.verificationCode,
      });
    },
  },
};
</script>

<style scoped lang="sass"></style>
