<template lang="pug">
  section
    h1
      | What's your phone number?
    VuePhoneNumberInput(
      v-model="phoneNum"
      @update="phoneUpdated"
    )
    //- recaptcha makes setting disabled via vue impossible, so we hide real button until valid
    //- we still want button to be in the DOM so recaptcha can attach at mount time, so using 'hidden'
    VButton#recaptcha(
      kind="primary"
      text="Next"
      v-show="isValid"
    )
    VButton(
      kind="primary"
      text="Next"
      v-show="!isValid"
      disabled
    )
    p
      | {{ error }}
</template>

<script>
import firebase from 'firebase';
import VuePhoneNumberInput from 'vue-phone-number-input';
import 'vue-phone-number-input/dist/vue-phone-number-input.css';

export default {
  name: 'RegisterPhone',
  components: {
    VuePhoneNumberInput,
  },
  props: {
    error: String,
  },

  mounted() {
    this.recaptchaVerifier = new firebase.auth.RecaptchaVerifier('recaptcha', {
      size: 'invisible',
      callback: res => {
        console.log('res:', res);
        this.submit();
      },
      'expired-callback': () => {
        console.warn('captcha expired');
      },
    });
    this.recaptchaVerifier.render();
  },

  data() {
    return {
      phone: '',
      recaptchaVerifier: null,
      phoneNum: '',
      isValid: false,
    };
  },

  methods: {
    submit() {
      this.$emit('submit', {
        phoneNumber: this.phone,
        recaptchaVerifier: this.recaptchaVerifier,
      });
    },
    phoneUpdated(value) {
      this.isValid = value.isValid;
      this.phone = value.e164;
    },
  },
};
</script>

