<template lang="pug">
section
  h1
    | What's your gender?
  label.radio(:class="{'checked': gender === 'm'}")
    | Male
    input(
      v-model="gender"
      type="radio"
      name="gender"
      value="m"
    )
  label.radio(:class="{'checked': gender === 'f'}")
    | Female
    input(
      v-model="gender"
      type="radio"
      name="gender"
      value="f"
    )
  label.radio(:class="{'checked': gender === 'o'}")
    | Non-Binary / Other
    input(
      v-model="gender"
      type="radio"
      name="gender"
      value="o"
    )
  VButton(
    kind="primary"
    :disabled="!gender"
    text="Next"
    @click.native="submit"
  )
  .why
    | We collect gender so hosts can get a read on who is coming.
</template>

<script>
export default {
  name: 'RegisterGender',

  props: {
    initialGender: String,
  },

  data() {
    return {
      gender: this.initialGender,
    };
  },

  computed: {
    isValid() {
      return this.gender != null;
    },
  },

  methods: {
    submit() {
      this.$emit('submit', {
        gender: this.gender,
      });
    },
  },
};
</script>

<style scoped lang="sass">
.why
  margin-top: 50px
</style>
