<template lang="pug">
section(v-if="user !== undefined")
  RegisterNavigation(@back="back")
  RegisterName(
    v-if="!user && !fullNameSubmitted"
    :initialFullName="fullName"
    @submit="setFullName($event)"
  )
  RegisterGender(
    v-if="!user && fullNameSubmitted && !genderSubmitted"
    :initialGender="gender"
    @submit="setGender($event)"
  )
  RegisterPhone(
    v-if="!user && fullNameSubmitted && genderSubmitted && !logInConfirmation"
    :error="logInError"
    @submit="sendVerification($event)"
  )
  RegisterPhoneVerification(
    v-if="!user && logInConfirmation"
    :error="logInError"
    @submit="verifyNumber($event)"
  )
  RegisterGroup(v-if="user")
</template>

<script>
import { mapState } from 'vuex';
import RegisterNavigation from '@/components/register/RegisterNavigation';
import RegisterName from '@/components/register/RegisterName';
import RegisterGender from '@/components/register/RegisterGender';
import RegisterPhone from '@/components/register/RegisterPhone';
import RegisterPhoneVerification from '@/components/register/RegisterPhoneVerification';
import RegisterGroup from '@/components/register/RegisterGroup';

export default {
  name: 'register',
  components: {
    RegisterNavigation,
    RegisterName,
    RegisterGender,
    RegisterPhone,
    RegisterPhoneVerification,
    RegisterGroup,
  },

  data() {
    return initialData();
  },

  computed: {
    ...mapState('auth', {
      user: state => state.user,
      logInError: state => state.logInError,
      logInConfirmation: state => state.logInConfirmation,
    }),
  },

  methods: {
    setFullName({ fullName }) {
      this.fullName = fullName;
      this.fullNameSubmitted = true;
    },
    setGender({ gender }) {
      this.gender = gender;
      this.genderSubmitted = true;
    },
    sendVerification({ phoneNumber, recaptchaVerifier }) {
      this.$store.dispatch('auth/logInWithPhoneNumber', { phoneNumber, recaptchaVerifier });
    },
    verifyNumber({ verificationCode }) {
      this.$store.dispatch('auth/verifyPhoneNumber', { verificationCode }).then(() => {
        const { fullName, gender } = this;
        return this.$store.dispatch('auth/updateProfile', { fullName, gender });
      });
    },
    back() {
      if (!this.user && !this.logInConfirmation) {
        if (this.genderSubmitted) {
          this.genderSubmitted = false;
        } else {
          this.fullNameSubmitted = false;
        }
      } else if (!this.user && this.logInConfirmation) {
        this.$store.commit('auth/CLEAR_LOGIN_ATTEMPT');
      } else if (this.user) {
        this.$store.dispatch('auth/logOut').then(() => {
          Object.assign(this, initialData());
        });
      }
    },
  },
};

function initialData() {
  return {
    fullName: '',
    fullNameSubmitted: false,
    gender: null, // m, f, o, or null
    genderSubmitted: false,
  };
}
</script>

<style scoped lang="sass">


section /deep/ button.primary,
section /deep/ .primary[type="button"]
  margin-top: 40px
</style>
