<template lang="pug">
section
  h1
    | What's your full name?
  VInput(
    v-model="fullName"
    type="text"
    placeholder="Full Name"
    required
  )
  VButton(
    kind="primary"
    text="Next"
    :disabled="!isValid"
    @click.native="submit"
  )
</template>

<script>
export default {
  name: 'RegisterName',
  props: {
    initialFullName: String,
  },

  data() {
    return {
      fullName: this.initialFullName,
    };
  },

  computed: {
    isValid() {
      // based on testing of iOS, this seems to be validation
      return this.normalizedFullName.length > 0;
    },
    normalizedFullName() {
      return this.fullName
        .split(' ')
        .filter(word => word != '')
        .map(word => word.slice(0, 1).toUpperCase() + word.slice(1))
        .join(' ');
    },
  },

  methods: {
    submit() {
      this.$emit('submit', {
        fullName: this.normalizedFullName,
      });
    },
  },
};
</script>

<style scoped lang="sass"></style>
